.scroll3D {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width: none;
  background-color: #000;
  color: #fff;
  font-size: calc(var(--index) * .8);
  font-family: raleway_c, sans-serif;
  line-height: 1.75;
  height: var(--depth);
  font-weight: 300;
}

:root {
  --index: calc(1vw + 1vh);
  --gutter: 30px;
  --side-small: 26;
  --side-big: 36;
  --depth: 4000px;
  --transition: .75s cubic-bezier(.075, .5, 0, 1)
}

.scroll3D::-webkit-scrollbar {
  display: none;
}
