.scroll3D__frame-media_left {
  right: calc(var(--side-small) /2 * var(--index) + var(--gutter)*30)
}

@media screen and (max-width: 1220px) {
  .scroll3D__frame-media_left {
    right: calc(var(--side-small) /2 * var(--index) + var(--gutter)*15)
  }
}
@media screen and (max-width: 800px) {
  .scroll3D__frame-media_left {
    right: calc(var(--side-small) /2 * var(--index) + var(--gutter)*10)
  }
}